import axios from 'axios';
import { serverUrl } from '../util';

const ProjectApi = {
  getMyProject: async (param) => {
    let result;
    try {
      result = await axios.get(`${serverUrl}/project/user/${param}`, {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          Accept: '*/*',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  getCurrentProject: async (param) => {
    let result;
    try {
      result = await axios.get(`${serverUrl}/project/${param}`, {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          Accept: '*/*',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  postProject: async (param) => {
    let result;

    try {
      result = await axios.post(`${serverUrl}/project`, param, {
        headers: {
          Accept: '*/*',
          'Content-Type':
            'multipart/form-data; boundary=WebKitFormBoundaryqTqJIxvkWFYqvP5s',
          'Access-Control-Allow-Origin': '*',
          'cache-control': 'no-cache',
        },
      });
    } catch (e) {
      return e.response;
    }

    return result;
  },

  getMainProject: async (param) => {
    let result;
    try {
      result = await axios.get(`${serverUrl}/project`, {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          Accept: '*/*',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  getProjectOne: async (param) => {
    let result;
    try {
      result = await axios.get(`${serverUrl}/project/${param}`, {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          Accept: '*/*',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  getAppliedCount: async (param) => {
    let result;
    try {
      result = await axios.get(`${serverUrl}/member/project?id=${param}`, {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          Accept: '*/*',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  //     
  getAppplyListMe: async (param) => {
    let result;
    try {
      result = await axios.get(`${serverUrl}/member/by-user/${param}`, {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          Accept: '*/*',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  // _API( )  : /page/project/ProjectApply.js
  postApplyProject: async (param) => {
    let result;
    try {
      result = await axios.post(`${serverUrl}/member`, param, {
        headers: {
          Accept: '*/*',
          'Content-Type':
            'multipart/form-data; boundary=WebKitFormBoundaryqTqJIxvkWFYqvP5s',
          'Access-Control-Allow-Origin': '*',
          'cache-control': 'no-cache',
        },
      });
    } catch (e) {
      return e.response;
    }
    return result;
  },
  modifyApplyProject: async (param) => {
    let result;
    try {
      result = await axios.put(
        `${serverUrl}/member/${param.get('id')}`,
        param,
        {
          headers: {
            Accept: '*/*',
            'Content-Type':
              'multipart/form-data; boundary=WebKitFormBoundaryqTqJIxvkWFYqvP5s',
            'Access-Control-Allow-Origin': '*',
            'cache-control': 'no-cache',
          },
        },
      );
    } catch (e) {
      return e.response;
    }
    return result;
  },
  patchProject: async (param) => {
    let result;

    try {
      //  param projectId  ...
      // withHeader  path="/app/projectUpdate/:id"
      // ${serverUrl}/project/${param.projectId}  <=  project/:id  ?
      // -> ProjectUpdate.js  param append(projectId) !
      result = await axios.patch(
        `${serverUrl}/project/${param.get('projectId')}`,
        param,
        {
          headers: {
            Accept: '*/*',
            'Content-Type':
              'multipart/form-data; boundary=WebKitFormBoundaryqTqJIxvkWFYqvP5s',
            'Access-Control-Allow-Origin': '*',
            'cache-control': 'no-cache',
          },
        },
      );
    } catch (e) {
      return e.response;
    }

    return result;
  },
  deleteProject: async (param) => {
    let result;
    try {
      result = await axios.delete(`${serverUrl}/project/${param}`);
    } catch (error) {
      return error.response;
    }
    return result;
  },
};
export default ProjectApi;
