import React, { useState, useEffect } from 'react';
import ReactHtmlParser from 'react-html-parser';
import {
  Row,
  Col,
  Input,
  Button,
  Radio,
  Select,
  DatePicker,
  Typography,
  Checkbox,
  Space,
  Modal,
} from 'antd';
import { Card, CardGroup, ListGroup } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { UploadOutlined, InboxOutlined } from '@ant-design/icons';
import { useSelector, useDispatch } from 'react-redux';
import { projectAction } from '../../redux/module/project/projectAction';
import { projectAPI } from '../../api';
import moment from 'moment';

const { TextArea } = Input;
const { Title, Text } = Typography;

function ProjectApply(props) {
  const user = useSelector((state) => {
    return state.user;
  });

  const list = useSelector((state) => {
    return state.project;
  });
  const dispatch = useDispatch();
  useEffect(() => {
    initProjectApply();
    return () => {};
  }, []);
  useEffect(() => {
    handleCheck();
  }, [list.currentProject]);
  const handleCheck = () => {
    if (list.currentProject !== null && user.userInfo !== null) {
      if (list.currentProject.userId === user.userInfo.id) {
        alert('   .');
        props.history.push(`/app/detail/${props.match.params.id}`);
      }
    }
  };
  // userId,      //user   
  //   projectId, //    
  //   name       //  
  //   appliedTime //
  //   appliedPosition, // 
  //   comments,        // 
  //   portfolioFile,   //  
  //   portfolioUrl,    //url 
  //   description;     //  
  const [appliedTime, setAppliedTime] = useState(null);
  const [appliedPosition, setAppliedPosition] = useState(null);
  const [portfolioUrl, setPortfolioUrl] = useState('');
  const [portfolioFile, setPortfolioFile] = useState(null);
  const [comments, setComments] = useState(null);
  const [onModal, setOnModal] = useState(false);
  const [agree, setAgree] = useState(false);
  const [radio, setRadio] = useState('url');

  //    
  const initProjectApply = async () => {
    if (user.userInfo !== null) {
      await dispatch(projectAction.getProjectOne(props.match.params.id));
    } else {
      alert('  .');
      props.history.push('/user/login');
    }
  };
  // 
  const handleAppliedTime = (e) => {
    setAppliedTime(e);
  };
  // 
  const handleAppliedPosition = (e) => {
    setAppliedPosition(e.target.value);
  };
  // 
  const handleRadio = (e) => {
    setRadio(e.target.value);
  };
  // 
  const handleComments = (e) => {
    setComments(e.target.value);
  };

  const handlePortfolioUrl = (e) => {
    setPortfolioUrl(e.target.value);
  };

  const handlePortfolioFile = (e) => {
    setPortfolioFile(e);
  };

  //    
  const handleSubmit = async () => {
    const formData = new FormData();
    let res = null;
    let utcFormat = 'yyyy-MM-DD HH:mm:ss';

    let applyDate = moment
      .utc(appliedTime, utcFormat)
      .local()
      .format(utcFormat);
    if (agree === false) {
      alert(' ');
    } else if (appliedTime === null) {
      alert('  ');
    } else if (appliedPosition === '') {
      alert('   ');
    } else if (radio === 'url') {
      if (portfolioUrl !== '') {
        formData.append('portfolioUrl', portfolioUrl);
        formData.append('userId', user.userInfo.id);
        formData.append('projectId', list.currentProject.id);
        formData.append('name', user.userInfo.name);
        formData.append('appliedTime', applyDate);
        formData.append('appliedPosition', appliedPosition);
        formData.append('comments', comments);
        res = await projectAPI.postApplyProject(formData);
      } else {
        alert('  ');
      }
    } else {
      if (portfolioFile !== null) {
        formData.append('portfolioFile', portfolioFile); //[null]
        formData.append('userId', user.userInfo.id);
        formData.append('projectId', list.currentProject.id);
        formData.append('name', user.userInfo.name);
        formData.append('appliedTime', applyDate);
        formData.append('appliedPosition', appliedPosition);
        formData.append('comments', comments);
        res = await projectAPI.postApplyProject(formData);
      } else {
        alert('   ');
      }
    }
    if (res !== null && res.status === 201) {
      alert('  ');
      props.history.push(`/app/info/user/${user.userInfo.id}`);
    } else {
      // alert('');
    }
  };

  if (list.currentProject !== null && user.userInfo !== null) {
    return (
      <div
        style={{
          alignItems: 'center',
          marginTop: 30,
          // marginBottom: 50,
        }}
      >
        <Card
          style={{
            backgroundColor: '#00BFFF',
            borderColor: ' #00BFFF',
            marginLeft: '20%',
            marginRight: '20%',
            borderRadius: '30px',
          }}
        >
          <Row justify="center" style={{}}>
            <Title
              style={{
                fontSize: '70px',
                color: '#FFFF',
                textShadow: ' 0 1px #00BFFF, 1px 0 #00BFFF',
                marginTop: '3.5%',
              }}
            >
              {list.currentProject.name}
            </Title>
          </Row>
        </Card>
        <br></br>
        <Row justify="center">
          <Title level={3}> </Title>
        </Row>
        <hr></hr>
        <Card
          style={{
            border: '1.5px solid ',
            borderColor: ' #00BFFF',

            marginLeft: '10%',
            marginRight: '10%',
          }}
        >
          <form>
            <Row
              style={{ marginTop: '2%', marginLeft: '30%', marginRight: '30%' }}
            >
              <Col span={10}>
                <Title level={5}></Title>
              </Col>
              <Col span={14}>
                <a
                  onClick={() => {
                    setOnModal(true);
                  }}
                  style={{}}
                >
                   
                </a>
              </Col>
            </Row>
            <Row
              style={{ marginTop: '2%', marginLeft: '30%', marginRight: '30%' }}
            >
              <Col span={10}>
                <Title level={5}></Title>
              </Col>
              <Col span={14}>
                <DatePicker
                  showTime
                  onChange={handleAppliedTime}
                  value={appliedTime}
                />
              </Col>
            </Row>
            <br></br>
            <Row gutter={[]} style={{ marginLeft: '30%', marginRight: '30%' }}>
              <Col span={10}>
                <Title level={5}> </Title>
              </Col>
              <Col span={14}>
                <Input
                  style={{ width: '200px' }}
                  onChange={handleAppliedPosition}
                />
              </Col>
            </Row>
            <br></br>
            <Row gutter={[]} style={{ marginLeft: '30%', marginRight: '30%' }}>
              <Col span={10}>
                <Title level={5}></Title>
              </Col>
              <Col span={14}>
                <Radio.Group value={radio} onChange={handleRadio}>
                  <Space direction="vertical">
                    <Radio value={'url'}>
                      url
                      {radio === 'url' ? (
                        <Input
                          placeholder="url   "
                          style={{ width: 300 }}
                          onChange={handlePortfolioUrl}
                        />
                      ) : null}
                    </Radio>
                    <Radio value={'file'}>
                       
                      {radio === 'file' ? (
                        <input
                          style={{ width: 250 }}
                          type="file"
                          onChange={(e) => {
                            handlePortfolioFile(e.target.files[0]);
                          }}
                        ></input>
                      ) : null}
                    </Radio>
                  </Space>
                </Radio.Group>
              </Col>
            </Row>

            <br></br>
            <Row
              gutter={[]}
              style={{
                marginLeft: '30%',
                marginRight: '30%',
                marginBottom: '2%',
              }}
            >
              <Col span={10}>
                <Title level={5}></Title>
              </Col>
              <Col span={14}>
                <TextArea
                  onChange={handleComments}
                  rows={4}
                  style={{ width: 290 }}
                  placeholder="   "
                />
              </Col>
            </Row>
          </form>
        </Card>
        <br></br>
        <Row gutter={[10, 10]} justify="center">
          <Col>
            <Button
              style={{ backgroundColor: '#00BFFF', color: '#FFFF' }}
              onClick={handleSubmit}
            >
              
            </Button>
          </Col>
          <Col>
            <Link to={{ pathname: `/main` }}>
              <Button type="primary" danger>
                
              </Button>
            </Link>
          </Col>
        </Row>
        <Modal
          footer={[]}
          title={''}
          visible={onModal}
          width="1100px"
          onCancel={() => setOnModal(false)}
          style={{ overflowY: 'scroll' }}
        >
          <Checkbox
            value={agree}
            onClick={() => {
              setAgree(!agree);
            }}
          >
            
          </Checkbox>

          <div style={{ padding: '20px', background: 'rgb(240, 248, 255)' }}>
            <h5>
                  You Make Me    
                   .
            </h5>
            <br></br>
            <h5> 1 () </h5>        
             ( ' ' )      
            ('' )    (''
            ) ,     .
            <br></br>
            <br></br>
            <h5> 2 ( )</h5>
            1.        
            . 2.        
                . 3.   
               .
            <br></br>
            <br></br>
            <h5> 3 (    )</h5>
            ''         
            .
          </div>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <Button
              disabled={!agree}
              onClick={() => {
                setOnModal(false);
              }}
            >
              
            </Button>
          </div>
        </Modal>
      </div>
    );
  } else {
    return <></>;
  }
}
export default ProjectApply;
