import React, { useState } from 'react';

import { Row, Col, Button, Typography } from 'antd';

import { useSelector } from 'react-redux';

import { projectAPI } from '../../../api';
import moment from 'moment';
import { s3Bucket } from '../../../util';

const { Title } = Typography;

function ProjectApply(props) {
  // usestate          /       / usestate    
  const user = useSelector((state) => {
    return state.user;
  });
  const list = useSelector((state) => {
    return state.project;
  });

  // userId,      //user   
  //   projectId, //    
  //   name       //  
  //   appliedTime //
  //   appliedPosition, // 
  //   comments,        // 
  //   portfolioFile,   //  
  //   portfolioUrl,    //url 
  //   description;     //  
  const [appliedTime, setAppliedTime] = useState(null);
  const [appliedPosition, setAppliedPosition] = useState(null);
  const [portfolioUrl, setPortfolioUrl] = useState('');
  const [portfolioFile, setPortfolioFile] = useState(null);
  const [comments, setComments] = useState(null);

  const [radio, setRadio] = useState('url');

  // 
  const handleAppliedTime = (e) => {
    setAppliedTime(e);
  };
  // 
  const handleAppliedPosition = (e) => {
    setAppliedPosition(e.target.value);
  };
  // 
  const handleRadio = (e) => {
    setRadio(e.target.value);
  };
  // 
  const handleComments = (e) => {
    setComments(e.target.value);
  };

  const handlePortfolioUrl = (e) => {
    setPortfolioUrl(e.target.value);
  };

  const handlePortfolioFile = (e) => {
    setPortfolioFile(e);
  };

  //    
  const handleSubmit = async () => {
    const formData = new FormData();
    let utcFormat = 'yyyy-MM-DD HH:mm:ss';

    let applyDate = moment
      .utc(appliedTime, utcFormat)
      .local()
      .format(utcFormat);

    formData.append('userId', user.userInfo.id);
    formData.append('projectId', list.currentProject.id);
    formData.append('name', user.userInfo.name);
    formData.append('appliedTime', applyDate);
    formData.append('appliedPosition', appliedPosition);

    if (radio === 'url') {
      formData.append('portfolioUrl', portfolioUrl);
    } else {
      formData.append('portfolioFile', portfolioFile); //[null]
    }

    formData.append('comments', comments);

    const res = await projectAPI.postApplyProject(formData);
  };
  if (list.currentProject !== null) {
    return (
      <div
        style={{
          alignItems: 'center',
          marginLeft: 40,
          marginRight: 40,
          marginTop: 50,
          marginBottom: 50,
        }}
      >
        <Row justify="center">
          <Title>{list.currentProject.name}</Title>
        </Row>
        <Row justify="center">
          <Title level={3}> </Title>
        </Row>
        <hr></hr>
        <form>
          <Row gutter={[]}>
            <div
              style={{
                width: '40%',
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <Title level={5}></Title>
            </div>
            <div
              style={{
                marginLeft: '10%',
                width: '50%',
                display: 'flex',
                justifyContent: 'flex-start',
              }}
            >
              {props.record.name}
            </div>
          </Row>

          <br></br>
          <Row gutter={[]}>
            <div
              style={{
                width: '40%',
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <Title level={5}> </Title>
            </div>
            <div
              style={{
                marginLeft: '10%',
                width: '50%',
                display: 'flex',
                justifyContent: 'flex-start',
              }}
            >
              <div>{props.record.appliedTime.substring(0, 10)}</div>
            </div>
          </Row>
          <br></br>
          <Row gutter={[]}>
            <div
              style={{
                width: '40%',
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <Title level={5}> </Title>
            </div>
            <div
              style={{
                marginLeft: '10%',
                width: '50%',
                display: 'flex',
                justifyContent: 'flex-start',
              }}
            >
              <div>{props.record.appliedPosition}</div>
            </div>
          </Row>
          <br></br>
          <Row gutter={[]}>
            <div
              style={{
                width: '40%',
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <Title level={5}></Title>
            </div>

            {props.record.portfolioUrl !== null ? (
              <div
                style={{
                  marginLeft: '10%',
                  width: '50%',
                  display: 'flex',
                  justifyContent: 'flex-start',
                }}
              >
                <a href={`${props.record.portfolioUrl}`} target="_blank">
                   url
                </a>
              </div>
            ) : (
              <div
                style={{
                  marginLeft: '10%',
                  width: '50%',
                  display: 'flex',
                  justifyContent: 'flex-start',
                }}
              >
                <a
                  target="_blank"
                  href={`${s3Bucket}${props.record.portfolioFile}`}
                >
                   
                </a>
              </div>
            )}
          </Row>
          <br></br>
          <Row gutter={[]}>
            <div
              style={{
                width: '40%',
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <Title level={5}></Title>
            </div>
            <div
              style={{
                marginLeft: '10%',
                width: '50%',
                display: 'flex',
                justifyContent: 'flex-start',
              }}
            >
              <div>{props.record.comments}</div>
            </div>
          </Row>
          <br></br>
          <br></br>
          <Row gutter={[80]} justify="center">
            <Col>
              <Button
                type="primary"
                onClick={() => {
                  props.handleOk();
                }}
              >
                
              </Button>
            </Col>
            <Col>
              <Button
                type="primary"
                danger
                onClick={() => {
                  props.handleReject();
                }}
              >
                
              </Button>
            </Col>
          </Row>
        </form>
      </div>
    );
  } else {
    return <></>;
  }
}
export default ProjectApply;
