import React, { useState, useEffect } from 'react';
import {
  Collapse,
  Row,
  Col,
  Typography,
  Input,
  Button,
  Select,
  Table,
  Modal,
  Tag,
} from 'antd';

import ProjectApplyModify from '../project/ProjectApplyModify';
import { projectAction } from '../../redux/module/project/projectAction';
import { useSelector, useDispatch } from 'react-redux';
import { projectAPI, userAPI } from '../../api';
import moment from 'moment';
import { userAction } from '../../redux/module/user/userAction';
import Constant from '../../redux/actionType';
const { Panel } = Collapse;

const { Title, Paragraph, Text } = Typography;

const { Option } = Select;

//   
function callback(key) {}

const del_msg =
  '           ,            ';

function UserInfo(props) {
  const [record, setRecord] = useState(null);
  const [name, setName] = useState('');
  const [visible, setVisible] = useState(false);
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [jobTitle, setJobTitle] = useState('');
  const [checkPassword, setCheckPassword] = useState('');
  const [check, setCheck] = useState(true);
  const handleInit = async () => {
    if (user.userInfo !== null) {
      setName(user.userInfo.name);
      setPassword(user.userInfo.password);
      setJobTitle(user.userInfo.jobTitle);
    }
  };
  const user = useSelector((state) => {
    return state.user;
  });

  const list = useSelector((state) => {
    return state.project;
  });

  const dispatch = useDispatch();
  useEffect(() => {
    initInfoUser();
    return () => {
      handleLeave();
    };
  }, []);
  const handleLeave = () => {
    dispatch({ type: Constant.LEAVE_PROJECT });
  };
  useEffect(() => {
    handleInit();
  }, [user.userInfo]);

  const initInfoUser = async () => {
    if (user.userInfo == null) {
      props.history.push('/main');
    }
    await dispatch(projectAction.getProjectOne(props.match.params.id));
    await dispatch(projectAction.getAppplyListMe(user.userInfo.id));
  };
  // 
  const handleName = (e) => {
    setName(e.target.value);
  };
  const handlePassword = (e) => {
    setPassword(e.target.value);
  };
  const handleNewPassword = (e) => {
    setNewPassword(e.target.value);
  };

  const CStatus = (e) => {
    if (e.status === 'approved') {
      return <Tag color="green"> </Tag>;
    } else if (e.status === 'pending') {
      return <Tag color="blue"> </Tag>;
    } else if (e.status === 'rejected') {
      return <Tag color="red"> </Tag>;
    }
  };
  const applyListC = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      render: (text, record) => {
        return <div>{record.name}</div>;
      },
    },
    {
      title: '',
      dataIndex: 'position',
      key: 'position',
      render: (text, record) => {
        return <div>{record.appliedPosition}</div>;
      },
    },
    {
      title: '',
      dataIndex: 'approval',
      key: 'approval',
      render: (text, record) => {
        return (
          <div>
            <CStatus {...record} />
          </div>
        );
      },
    },
    {
      title: '',
      dataIndex: 'date',
      key: 'date',
      render: (text, record) => {
        return <div>{record.createTime}</div>;
      },
    },
    {
      title: '',
      dataIndex: 'applyModify',
      key: 'applyModify',
      render: (text, record, index) => {
        return (
          <Button
            type="primary"
            onClick={() => {
              setRecord(record);
              setVisible(true);
            }}
          >
            
          </Button>
        );
      },
    },
  ];
  const handleSubmit = (e) => {
    const formData = new FormData();
    if (e === 'name') {
      formData.append('name', name);
      dispatch(userAction.modifyUser({ data: formData, id: user.userInfo.id }));
    } else if (e === 'password') {
      if (password !== newPassword) {
        alert(' .');
      } else {
        formData.append('password', password);
        dispatch(
          userAction.modifyUser({ data: formData, id: user.userInfo.id }),
        );
      }
    } else {
      formData.append('jobTitle', jobTitle);
      dispatch(userAction.modifyUser({ data: formData, id: user.userInfo.id }));
    }
  };
  const handleJobTitle = (e) => {
    setJobTitle(e.target.value);
  };
  const handleDelete = async () => {
    const res = await userAPI.deleteUser({
      id: user.userInfo.id,
    });
    if (res.status !== 200) {
      alert('.');
    } else {
      localStorage.clear();
      alert(res.data);
      props.history('/main');
    }
  };
  const handleSetCheckPassword = (e) => {
    setCheckPassword(e.target.value);
  };
  const handleCheck = async () => {
    const res = await userAPI.login({
      email: user.userInfo.email,
      password: checkPassword,
    });
    if (res.status !== 200) {
      setCheck(true);
      alert(' .');
    } else {
      setCheck(false);
    }
  };

  if (list.currentProject != null) {
    return (
      <div
        style={{
          alignItems: 'center',
          marginLeft: 40,
          marginRight: 40,
          marginTop: 50,
          marginBottom: 50,
        }}
      >
        <Row justify="center">
          <Title level={2}> </Title>
        </Row>
        <br />
        <hr></hr>
        <Row gutter={[100, 0]}>
          <Col>
            <Title level={4}></Title>
          </Col>
          <Col>
            <Input value={name} onChange={handleName} />
          </Col>
          <Row gutter={[20, 0]}>
            <Col>
              <Button
                onClick={() => {
                  handleSubmit('name');
                }}
              >
                
              </Button>
            </Col>
            <Col>
              <Button></Button>
            </Col>
          </Row>
        </Row>
        <hr></hr>
        <Collapse defaultActiveKey={['4']} onChange={callback}>
          <Panel header=" " key="2">
            <Row justify="center">
              <Col style={{ width: 250 }}>
                <Title level={5}> </Title>
                <Input
                  type="password"
                  value={password}
                  onChange={handlePassword}
                  placeholder="  "
                ></Input>
                <Title level={5}> </Title>
                <Input
                  type="password"
                  value={newPassword}
                  onChange={handleNewPassword}
                  placeholder="  "
                ></Input>
              </Col>
            </Row>
            <br></br>
            <Row gutter={[10]} justify="center">
              <Col>
                <Button onClick={() => handleSubmit('password')}></Button>
              </Col>
              <Col>
                <Button></Button>
              </Col>
            </Row>
          </Panel>
          <Panel header=" " key="3">
            <Row justify="center">
              <Col>
                <Title level={5}>  </Title>
                <Input value={jobTitle} onChange={handleJobTitle}></Input>
              </Col>
            </Row>
            <br></br>
            <Row gutter={[10]} justify="center">
              <Col>
                <Button
                  onClick={() => {
                    handleSubmit('appliedPosition');
                  }}
                >
                  
                </Button>
              </Col>
              <Col>
                <Button></Button>
              </Col>
            </Row>
          </Panel>

          <Panel
            header="  "
            key="4"
            //style={{ border: 'solid #00BFFF', background: '#00BFFF' }}
          >
            {list.memberList !== null && (
              <Table
                style={{}}
                columns={applyListC}
                dataSource={list.memberList
                  .filter((value) => {
                    return value.ownerUserId !== user.userInfo.id;
                  })
                  .map((value, key) => {
                    return { ...value, key: key };
                  })}
              />
            )}
          </Panel>
          <Panel header=" " key="5">
            <Paragraph
              style={{
                justifyContent: 'center',
              }}
            >
              <pre>{del_msg}</pre>
              <Input
                style={{ width: '85%' }}
                value={checkPassword}
                onChange={handleSetCheckPassword}
              ></Input>
              <Button onClick={handleCheck}> </Button>
            </Paragraph>
            <Row justify="center">
              <Button
                disabled={check}
                onClick={() => {
                  handleDelete();
                }}
              >
                 
              </Button>
              <Button></Button>
            </Row>
          </Panel>
        </Collapse>
        <Modal
          title="  "
          centered
          visible={visible}
          onOk={() => setVisible(false)}
          onCancel={() => setVisible(false)}
          width={900}
          footer={[]}
        >
          <ProjectApplyModify
            onOk={() => setVisible(false)}
            onCancel={() => setVisible(false)}
            record={record}
            {...props}
          />
        </Modal>
      </div>
    );
  } else {
    return <></>;
  }
}

export default UserInfo;

